

import {
	Button,
	Icon,
	Panel,
	PanelBody,
	PanelRow,
	Placeholder,
	SelectControl,
	Spinner,
	TextControl,
	ToggleControl,
} from '@wordpress/components';


import { Fragment, Component, } from '@wordpress/element';

import { __ } from '@wordpress/i18n';

class Post extends Component {

	constructor() {
		super(...arguments);

		this.state = {
			exampleSelect: '',
		}
	}

	render() {

		const { exampleSelect } = this.state

		console.log(exampleSelect);

		return (
			<Fragment>
				<TextControl
					help={__('Use PanelRow to place controls inline.', 'wholesome-plugin')}
					label={__('Example Text 2', 'wholesome-plugin')}
					onChange={(exampleSelect) => this.setState({ exampleSelect })}
					value={exampleSelect}
				/>
			</Fragment>
		)
	}
}

export default Post
